"use client";

import { Button } from "@repo/ui/components/button";
import { trpc } from "@/lib/trpc";
import { useEffect, useState } from "react"; // <--- Import

export default function Page() {
  const hello = trpc.hello.useQuery({ text: "Developere" });
  
  // Řešení pro Hydration Mismatch u data/času:
  // Čas zobrazíme, až když jsme si jistí, že běžíme v prohlížeči.
  const [isClient, setIsClient] = useState(false);
  useEffect(() => {
    setIsClient(true);
  }, []);

  return (
    <div className="flex min-h-screen flex-col items-center justify-center gap-8 p-10 bg-background">
      <h1 className="text-4xl font-bold tracking-tight">Fullstack Monorepo</h1>
      
      <div className="p-8 border rounded-xl shadow-sm bg-card min-w-[350px] text-center space-y-4">
        <h2 className="text-lg font-semibold text-muted-foreground">Odpověď ze serveru:</h2>
        
        {hello.isLoading ? (
          <p className="animate-pulse">Načítám data...</p>
        ) : (
          <div className="space-y-2">
            <p className="text-2xl font-bold text-primary">
              {hello.data?.greeting}
            </p>
            
            {/* Zobrazíme čas pouze na klientovi, aby se nelišil od serveru */}
            {isClient && hello.data?.time && (
              <p className="text-sm text-muted-foreground">
                Čas serveru: {new Date(hello.data.time).toLocaleTimeString()}
              </p>
            )}
          </div>
        )}
      </div>

      <div className="flex gap-4">
        <Button onClick={() => hello.refetch()}>Znovu načíst</Button>
        <Button variant="outline">Jen Design Tlačítko</Button>
      </div>
    </div>
  );
}